<template>
  <div class="loading-latout position-fixed top-0 start-0" v-if="isLoading">
    <semipolar-spinner
      :animation-duration="2000"
      :size="80"
      color="#b41f23"
      class="loading"
    />
  </div>
  <div class="layout-main">
    <div class="cms-page">
      <div class="cms-page-head">
        <div class="cms-page-head-wrap">
