            <h1 class="cms-page-title">Payments</h1>
          </div>
        </div>
      </div>
      <div class="container-fluid container-cms">
        <div class="cms-page-data">
          <div class="payment-tables">
            <div class="radio-group-tabs">
              <div class="radio-tab radio-tab-default radio-group-tabs-radio" :class="{ 'radio-tab-active': selectionOption === 'deposit' }">
                <input type="radio" class="radio-tab-input" id="deposit" name="payments-categories" value="deposit" checked v-model="selectionOption">
                <label for="deposit" class="radio-tab-block">
                  <span class="radio-tab-label">Deposit methods</span>
                </label>
              </div>
              <div class="radio-tab radio-tab-default radio-group-tabs-radio" :class="{ 'radio-tab-active': selectionOption === 'withdrawal' }">
                <input type="radio" class="radio-tab-input" id="withdrawal" name="payments-categories" value="withdrawal" v-model="selectionOption">
                <label for="withdrawal" class="radio-tab-block">
                  <span class="radio-tab-label">Withdrawal</span>
                </label>
              </div>
            </div>
            <div class="payment-tables-section">
              <h2 class="payment-tables-title" v-if="selectionOption === 'deposit'">Make deposits quick, secure, and without any free.</h2>
              <h2 class="payment-tables-title" v-else>Casino offers dozens of fast and secure withdrawal methods without any fee.</h2>
              <div class="payment-tables-table-wrap">
                <table class="payment-tables-table">
                  <thead>
                    <tr>
                      <th>Method</th>
                      <th>Type</th>
                      <th>Free</th>
                      <th>Processing Time</th>
                      <th>Limit</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                  <tbody v-if="selectionOption === 'deposit'">
                    <template v-for="item in depositeMethods" :key="item.name">
                      <tr>
                        <td data-title="Method">
                          <div class="payment-tables-card-wrap">
                            <div class="payment-tables-image-wrap">
                              <img :src="item.imgUrl" alt="">
                            </div>
                            <div class="payment-tables-image-label">CoinsPaid</div>
                          </div>
                        </td>
                        <td data-title="Type">
                          {{ item.type }}
                        </td>
                        <td data-title="Free">
                          {{ item.free }}
                        </td>
                        <td data-title="Processing Time">
                          {{ item.processingTime }}
                        </td>
                        <td data-title="Limit">
                          {{ item.limit }}
                        </td>
                        <td data-title="Action">
                          <a href="" class="payment-tables-btn deposit-btn link-btn link-btn-primary">Sign up</a>
                        </td>
                      </tr>
                    </template>
                  </tbody>
                  <tbody v-else>
                    <template v-for="item in withdrawalMethods" :key="item.name">
                      <tr>
                        <td data-title="Method">
                          <div class="payment-tables-card-wrap">
                            <div class="payment-tables-image-wrap">
                              <img :src="item.imgUrl" alt="">
                            </div>
                            <div class="payment-tables-image-label">CoinsPaid</div>
                          </div>
                        </td>
                        <td data-title="Type">
                          {{ item.type }}
                        </td>
                        <td data-title="Free">
